.paginatorDiv {
    display: flex;
    flex-direction: row;
    gap: 15px;
    justify-content: center;
    align-items: center;
    /* background-color: rgb(199, 199, 240); */
    margin: 30px 0;
}

.pageDiv {
    background-color: grey;
    border-radius: 50%;
    padding: 5px;
    width: 30px;
    height: 30px;
    text-align: center;
    cursor: pointer;
}
.pageDivChecked {
    background-color: green;
}